import React from 'react'
import FireRate from '../assets/Fire-Rating.png'
const Degree = () => {
    return (
        <div className='px-[20px] py-[20px] md:px-[100px] md:py-[30px]'>
            <div className='flex items-center gap-5 md:gap-10'>
                <span className='text-white font-medium text-[30px] md:text-[40px] background px-[10px] rounded-md'>Drcsi</span>
                <span className='font-normal text-base md:text-lg text-black'>Yannlarn drcsi</span>
            </div>
            <div className='background-light p-[30px] md:p-[60px] rounded-[45px] mt-[30px] flex flex-wrap justify-between items-center'>
                <div className='w-[95%] m-auto lg:w-[60%]'>
                    <h5>
                    A, B v ya F sinifli yannlar sndrmy qadir olan btn yannsndrnlr rqm v hrfl gstriln yann drcsin malikdirlr (msln, 13A, 55B). Nmr snaq raitind sndr bilcyi yannn lsn gstrir. Say n qdr ox olarsa, sndr bilcyi yann da bir o qdr byk olar. Mktub yuxardak kimi yann tsnifatn gstrir. C, D siniflri v elektrik simvolu ddi reytinq damr.
                    F sinifli yannlar sndrmy qadir olan btn yannsndrnlr 5, 15, 25 v 75 litr gnbaxan ya istifad edrk 4 etalon test sasnda reytinq malikdirlr. Ya avtomatik alana qdr qzdrlr v 2 dqiq vvlcdn yanmaa icaz verilir. Sonra yann sndrlr v yann sndrldkdn sonra 10 dqiq rzind yenidn alovlanma ba vermmlidir.
                    </h5>
                </div>
                <div className='w-[95%] m-auto lg:w-[40%]'>

                <img src={FireRate} alt="fire-rate" className='w-full object-cover' />
                </div>
            </div>
        </div>
    )
}

export default Degree